// SB content grid item Variables:
// --sb-content-grid-item-border-radius
// --sb-content-grid-item-border-color - outline
// --sb-content-grid-item-color -  color
// --sb-content-grid-item-margin
// --sb-content-grid-item-padding
// --sb-content-grid-item-height
// --sb-content-grid-item-max-height
// --sb-content-grid-item-text-align
// --sb-content-grid-item-shadow-subtle
// --sb-content-grid-item-interactive-border-color
// --sb-content-grid-item-active-color
// --sb-content-grid-item-active-border-color
// --sb-content-grid-item-active-border-hover-color

.sb-content-grid-item {
  background-color: var(
    --sb-content-grid-item-color,
    var(--sbt-color-background)
  );
  border: 1px solid
    var(--sb-content-grid-item-border-color, var(--sbt-theme-color-10));
  border-radius: var(
    --sb-content-grid-item-border-radius,
    var(--sbt-border-radius-small)
  );
  box-shadow: var(
    --sb-content-grid-item-shadow-subtle,
    var(--sbt-elevation-20)
  );
  max-height: var(--sb-content-grid-item-max-height, 15em);
  min-height: var(--sb-content-grid-item-height, 3em);
  padding: var(--sb-content-grid-item-padding, 10px);
  text-align: var(--sb-content-grid-item-height, left);
}

.sb-content-grid-item__interactive {
  cursor: pointer;

  &:hover {
    border-color: var(
      --sb-content-grid-item-interactive-border-color,
      var(--sbt-theme-color-50)
    );
    box-shadow: var(--sbt-elevation-40);
  }
}

.sb-content-grid-item_active {
  background-color: var(
    --sb-content-grid-item-active-color,
    var(--sbt-theme-color-20)
  );
  border-color: var(
    --sb-content-grid-item-active-border-color,
    var(--sbt-theme-color-50)
  );

  &:hover {
    border-color: var(
      --sb-content-grid-item-active-border-hover-color,
      var(--sbt-theme-color-40)
    );
  }
}
