.sb-skeleton {
  animation: skeletonKeyframes 1.2s ease-in-out infinite;
  background-color: var(--sbt-theme-tertiary-color-10);
  background-image: linear-gradient(
    90deg,
    var(--sbt-theme-tertiary-color-10),
    var(--sbt-theme-tertiary-color-20),
    var(--sbt-theme-tertiary-color-10)
  );
  background-repeat: no-repeat;
  background-size: 200px 100%;
  display: inline-block;
  line-height: var(--sbt-line-height-ui);
}

@keyframes skeletonKeyframes {
  0% {
    background-position: -200px 0;
  }

  100% {
    background-position: calc(200px + 100%) 0;
  }
}
