// SB Content Grid Variables:
// --sbt-content-grid-columns
// --sbt-content-grid-row-gap
// --sbt-content-grid-column-gap
// --sb-content-grid-item-width

.sb-content-grid {
  column-gap: var(--sbt-content-grid-column-gap, 16px);
  display: grid;
  grid-template-columns: var(
    --sbt-content-grid-columns,
    repeat(
      auto-fill,
      minmax(#{'min(100%, var(--sb-content-grid-item-width))'}, 1fr)
    )
  );
  row-gap: var(--sbt-content-grid-row-gap, 16px);
}

.sb-content-grid_smaller {
  --sb-content-grid-item-width: 100px;
}

.sb-content-grid_small {
  --sb-content-grid-item-width: 180px;
}

.sb-content-grid_medium {
  --sb-content-grid-item-width: 290px;
}

.sb-content-grid_large {
  --sb-content-grid-item-width: 350px;
}

.sb-content-grid_larger {
  --sb-content-grid-item-width: 550px;
}
